Site Name: SIR Annual Report 2015
Author: Sagetopia

Stylesheet: 481px and Up Stylesheet
******************************************************************/

.alignleft, .alignright {
	margin-bottom: 2em;
}

.alignleft {
	margin-right: 2em;
	float: left;
}

.alignright {
	margin-left: 2em;
	float: right;
	clear: right;
}

.boxed {
	&.alignright, &.alignleft {
		max-width: 200px;
	}
}

.icon-stats {
	.stat {
		.number, .icon {
			display: inline-block;
			vertical-align: middle;
		}

		.number {
			span {
				font-size: 3em;
			}
		}

		.icon {
			max-width: 200px;
		}
	}
}